import React, { forwardRef } from "react";

import { Trigger } from "@radix-ui/react-dropdown-menu";

import { StyledDropDownRoot } from "./DropDown.styles";
import { DropDownProps } from "./DropDown.types";
import DropDownMenu from "./DropDownMenu";

/**
 * @description Dropdown is built on top of [@radix-ui/react-dropdown-menu](https://www.radix-ui.com/docs/primitives/components/dropdown-menu)
 * @see https://www.radix-ui.com/docs/primitives/components/dropdown-menu
 */
const DropDown = forwardRef<HTMLDivElement, DropDownProps>(({
	children,
	trigger,
	side,
	align,
	sideOffset,
	dataQA,
	ariaLabel,
	$menuWidth,
	$menuHeight,
	$zIndex,
	onOpenChangeAction,
	radixDropdownMenuProps,
	isModal = false,
	isTriggerDisabled,
	...props
}, ref) => {
	const handleOpenChange = (open: boolean) => {
		if (onOpenChangeAction) {
			onOpenChangeAction(open);
		}
	};

	return (
		<StyledDropDownRoot
			onOpenChange={handleOpenChange}
			modal={isModal}
			{...radixDropdownMenuProps}
		>
			<Trigger asChild disabled={isTriggerDisabled}>
				{trigger}
			</Trigger>
			<DropDownMenu
				side={side}
				align={align}
				sideOffset={sideOffset}
				$menuWidth={$menuWidth}
				$menuHeight={$menuHeight}
				$zIndex={$zIndex}
				dataQA={dataQA}
				ariaLabel={ariaLabel}
				ref={ref}
				{...props}
			>
				{children}
			</DropDownMenu>
		</StyledDropDownRoot>
	);
});

DropDown.displayName = "DropDown";

export default DropDown;
